const initialState = {
    recipes: [],
    allRecipes: [],
    diets: [],
    details: [],
}

function rootReducer (state= initialState, action) {
    switch(action.type) {
        case "GET_RECIPES":
            return {
                ...state,
                recipes: action.payload,
                allRecipes: action.payload
            }
        case "ORDER_BY_NAME":
                let sortedArr = action.payload === "asc" ?
                    state.recipes.sort(function(a, b) {
                        if(a.title > b.title) {
                            return 1
                        }
                        if(b.title > a.title) {
                            return -1
                        }
                        return 0
                    }) :
                    state.recipes.sort(function(a,b) {
                        if(a.title > b.title) {
                            return -1
                        }
                        if(b.title > a.title) {
                            return 1
                        }
                        return 0
                    })
                    return {
                        ...state,
                        recipes: action.payload === "all" ?
                            state.allRecipes :
                            sortedArr
                    }
        case "ORDER_BY_HEALTH_SCORE":
            let sortedArray = action.payload === "asc" ?
                    state.recipes.sort(function(a, b) {
                        if(a.healthScore > b.healthScore) {
                            return 1
                        }
                        if(b.healthScore > a.healthScore) {
                            return -1
                        }
                        return 0
                    }) :
                    state.recipes.sort(function(a,b) {
                        if(a.healthScore > b.healthScore) {
                            return -1
                        }
                        if(b.healthScore > a.healthScore) {
                            return 1
                        }
                        return 0
                    })
                    return {
                        ...state,
                        recipes: action.payload === 'all' ?
                            state.recipes :
                            sortedArray
                    }
        case "CHARGE_DIETS":
            return {
                ...state,
                diets: action.payload,
            }
         case "FILTER_BY_DIETS":
                const allRecipes = state.recipes
                const boolean = []
                
                allRecipes.map(e => e.diets).forEach((f) => {
                    if(f.includes(action.payload)) { 
                        boolean.push(1)
                    } else {
                        boolean.push(0)
                    } 
                    
                })
                const dietFilter = []
                for(let i = 0; boolean.length > i; i++) {
                    if(boolean[i] === 1) {
                        dietFilter.push(allRecipes[i])
                    }
                }
                
                return {
                    ...state,
                    recipes: dietFilter
                } 
        case "GET_NAME_RECIPES":
            return {
                ...state,
                recipes: action.payload
            }
        case "POST_RECIPE":
            return {
                ...state
            }
        case "GET_DETAILS":
            return {
                ...state,
                details: action.payload
            }
        default:
            return state
    }
}

export default rootReducer