import React, {useState, useEffect} from "react"
import {chargeDiets, postRecipes} from "../actions"
import { useDispatch, useSelector } from "react-redux"
import Nav from "./Nav"
import style from "./ModulesCSS/Form.module.css"

export default function Form() {
    const dispatch = useDispatch()
    const diets = useSelector((state) => state.diets)
    
    const [input, setInput] = useState({
        title: "",
        summary: "",
        healthScore: "",
        diets: [],
        steps: [],
    })

    const [errors, setErrors] = useState({})

    useEffect(() => {
        dispatch(chargeDiets())
    }, [dispatch])

    function handleChange(e) {
        setInput({
            ...input,
            [e.target.name] : e.target.value
        })
        setErrors(validate({
            ...input,
            [e.target.name] : e.target.value
        }))
        console.log(input)
        console.log(errors)
    }
    function handleSelectDiets(e) {
        if(!input.diets.includes(e.target.value)) {
            setInput({
                ...input,
                diets: [...input.diets, e.target.value]
            })
        } else {
            setInput({
                ...input
            })
        }
        
    }
    function handleSubmit(e) {
        if(errors.name === "El título debe tener al menos 5 caracteres" || errors.summary === "Se requiere una descripción" || errors.healthScore === "Se requiere un Health Score") {
            alert("Faltan completar campos")
        }
        else {
            e.preventDefault()
            console.log(input)
            dispatch(postRecipes(input))
            alert("Receta creada!")
            setInput({
                title: "",
                summary: "",
                healthScore: "",
                diets: [],
                steps: [],
            })
        }
    }
    function validate(input) {
        let errors = []
        if(!input.title) {
            errors.title = "Se requiere un nombre"
        } else if(input.title.length < 4) {
            errors.title = "El nombre debe tener al menos 5 caracteres"
        }

        if(!input.summary) {
            errors.summary = "Se requiere una descripción"
        } else if (input.summary.length < 8) {
            errors.summary = "La descripción debe tener al menos 8 caracteres"
        }

        if(!input.healthScore) {
            errors.healthScore = "Se requiere un rating"
        } 

        return errors
    }
    function handleDeleteDiets(e) {
        setInput({
            ...input,
            diets: input.diets.filter(p => p !== e)
        })
    }

    return (
        <div className={style.Container}>
            <Nav/>
            <div className={style.ContainerForm}>
                <h2>INGRESA UNA RECETA</h2>
                <form onSubmit={(e) => handleSubmit(e)}>
                    <div>
                        <div>
                            <div  className={style.Input}>
                                <label>Nombre: </label>
                                <input
                                    type="text"
                                    value={input.title}
                                    name="title"
                                    onChange={(e) => handleChange(e)}
                                />
                            </div>
                            <p className={style.Parrafo}>
                                {errors.title && (
                                    <p className={style.Parrafo}>{errors.title}</p>
                                )}
                            </p>
                        </div>
                        
                        <div>
                            <div className={style.Input}>
                                <label>Descripción: </label>
                                <input
                                    type="text"
                                    value={input.summary}
                                    name="summary"
                                    onChange={(e) => handleChange(e)}
                                />
                            </div>
                            <p className={style.Parrafo}>
                                {errors.summary && (
                                    <p className={style.Parrafo}>{errors.summary}</p>
                                )}
                            </p>
                        </div>
                        <div>
                            <div className={style.Input}>
                                <label>Health Score: </label>
                                <input
                                    type="number"
                                    value={input.healthScore}
                                    name="healthScore"
                                    onChange={(e) => handleChange(e)}
                                />
                            </div>
                            <p className={style.Parrafo}>
                                {errors.summary && (
                                    <p className={style.Parrafo}>{errors.summary}</p>
                                )}
                            </p>
                        </div>
                        <div>
                            <div className={style.Input}>
                                <label>Pasos: </label>
                                <input
                                    type="text"
                                    value={input.steps}
                                    name="steps"
                                    onChange={(e) => handleChange(e)}
                                />
                            </div>
                        </div>
                        
                        <select onChange={(e) => handleSelectDiets(e)} className={style.Select}>
                            {diets.map((d) => {
                                return (
                                    <option value={d.name}>{d.name}</option>
                                )
                            })}
                        </select>

                        <button type="submit" className={style.Button}>Crear receta</button>
                    </div>
                </form>
                <div className={style.DeleteContainer}>
                    {input.diets.map(e => 
                        <div className={style.LittleBox}>
                            <p>{e}</p>
                            <button onClick={() => handleDeleteDiets(e)}>x</button>
                        </div>
                    )} 
                </div>
            </div>
        </div>
    )
}