import React from "react"
import { useDispatch, useSelector } from "react-redux"
import { useEffect } from "react"
import { getDetail } from "../actions"
import Nav from "./Nav"
import style from "./ModulesCSS/Detail.module.css"

export default function Detail(props) {
    const dispatch = useDispatch()

    useEffect(() => {
        dispatch(getDetail(props.match.params.id))
    }, [dispatch])

    const theRecipe = useSelector((state) => state.details)
    const objectLength = Object.keys(theRecipe).length
    console.log(theRecipe)
    
    return (
        <div>
            <Nav/>
            <div className={style.Container}>
                {
                    objectLength ?
                    <div className={style.ContainerDetails}>
                        {theRecipe.image ?
                            <img src={theRecipe.image} alt="Recipe" className={style.Img}></img> :
                            <img src="https://i.pinimg.com/564x/5f/8f/13/5f8f134daf12248113a63d9f7d0cae89.jpg" alt="Img game" width='300px' height='300px' className={style.Img}></img>
                        }
                        <div className={style.Detail}>
                            <h1>{theRecipe.title}</h1>
                            <p>Tipo de plato: {theRecipe.dishTypes}</p>
                            {theRecipe.createdInDb ?
                                <p>Tipo de dieta: {theRecipe.diets.map(e => e.name).join(", ")}</p> :
                                <p>Tipo de dieta: {theRecipe.diets.join(", ")}</p>
                            } 
                            {/* <p>Tipo de dieta: {theRecipe.diets.join(", ")}</p> */}
                            
                            <p>Health Score: {theRecipe.healthScore}</p>
                            <h4>Descripción:</h4>
                            <p className={style.Description}>
                                {theRecipe.summary.replace(/(<([^>]+)>)/gi, "")}
                            </p>
                            <h4>Paso a paso:</h4>
                            <ul className={style.Description}>
                                {theRecipe.createdInDb ?
                                    <p>{theRecipe.steps}</p> :
                                    theRecipe.analyzedInstructions.map(e => {return(<li>{e}</li>)})
                                }
                                
                            </ul>
                        </div>
                    </div> :
                    <p>Cargando...</p>
                }
            </div>
        </div>
    )
}