import React from "react";
import style from "./ModulesCSS/Card.module.css"

export default function Card({ title, image, diets }) {
    return(
        <div className={style.Container}>
            <img src={image} alt="food" className={style.Img}></img> 
            <div className={style.Detail}>
                <h4>{title}</h4>
                <p>{diets ? diets.join(", ") : "ketogenic, daily free"}</p> 
            </div>
        </div>
    )
}