import React from "react"
import style from "./ModulesCSS/Paginado.module.css"

export default function Paginado({recipesPerPage, allRecipes, paginado, currentPage}) {
    const pageNumbers = []

    for(let i=1; i<=Math.ceil(allRecipes/recipesPerPage); i++) {
        pageNumbers.push(i)
    }

    return (
        <nav className={style.Nav}>
            <button className={style.Button}>
                <a onClick={() => paginado(currentPage-1)}>Prev</a>
            </button>
            <ul>
                {pageNumbers &&
                    pageNumbers.map(number => 
                        currentPage === number ?
                        (
                            <button key={number} className={style.CurrentButton}>
                                <a onClick={() => paginado(number)} className={style.CurrentNumber}>{number}</a>
                            </button>
                        ) :
                        (
                            <button key={number} className={style.Button}>
                                <a onClick={() => paginado(number)} className={style.Number}>{number}</a>
                            </button>
                        )
                    )
                }
                
            </ul>
            <button className={style.Button}>
                <a onClick={() => paginado(currentPage+1)}>Next</a>
            </button>
        </nav>
    )
}