import { useEffect, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { Link } from "react-router-dom";
import { chargeDiets, filterByDiets, getRecipes, orderByHealthScore, orderByName } from "../actions";
import Card from "./Card";
import Nav from "./Nav";
import Paginado from "./Paginado";
import SearchBar from "./SearchBar";
import style from "./ModulesCSS/Home.module.css"
import Py from "./pythonExample";

export default function Home() {
    const dispatch = useDispatch()
    const allRecipes = useSelector((state) => state.recipes)
    const [currentPage, setCurrentPage] = useState(1)
    const [recipesPerPage, setRecipesPerPage] = useState(9)
    const indexOfLastRecipe = currentPage * recipesPerPage 
    const indexOfFirstRecipe = indexOfLastRecipe - recipesPerPage
    const currentRecipes = allRecipes?.slice(indexOfFirstRecipe, indexOfLastRecipe)


    const [orden, setOrden] = useState('')
    const allDiets = useSelector((state) => state.diets)

    const paginado = (pageNumber) => {
        setCurrentPage(pageNumber)
    }

    useEffect(() => {
        dispatch(getRecipes())
    }, [dispatch])
    useEffect(() => {
        dispatch(chargeDiets())
    }, [dispatch])

    function handleClick(e) {
        e.preventDefault()
        dispatch(getRecipes())
    }
    function handleSort(e) {
        e.preventDefault()
        dispatch(orderByName(e.target.value))
        setCurrentPage(1)
        setOrden(`Ordenado ${e.target.value}`)
    }
    function handleSortByHealthScore(e) {
        e.preventDefault()
        dispatch(orderByHealthScore(e.target.value))
        setCurrentPage(1)
        setOrden(`Ordenado ${e.target.value}`)
    }
    function handleFilterByDiets(e) {
        e.preventDefault()
        dispatch(filterByDiets(e.target.value))
        setCurrentPage(1)
    }




    return(
        <div className={style.Container}>
            <Nav/>
        <div className={style.ContainerFilters}>
            
            <button onClick={e => {handleClick(e)}} className={style.Button}>
                Volver a cargar todas las recetas
            </button>
            <div>
                <select onChange={e => {handleSort(e)}} className={style.Filter}>
                    <option value="all">Orden alfabético</option>
                    <option value="asc">A - Z</option>
                    <option value="desc">Z - A</option>
                </select>
                <select onChange={e => {handleSortByHealthScore(e)}} className={style.Filter}>
                    <option value="all">Health Score</option>
                    <option value="asc">Mayor Health Score</option>
                    <option value="desc">Menor Health Score</option>
                </select>
                <select onChange={e => {handleFilterByDiets(e)}} className={style.Filter}>
                    <option value="all">Tipo de dieta</option>
                    {allDiets.map((d) => {
                        return(
                            <option value={d.name}>{d.name}</option>
                        )
                    })}
                </select>
            </div>
            <SearchBar
                setCurrentPage={setCurrentPage}
            /> 
            <Py/>
            <Paginado
                recipesPerPage={recipesPerPage}
                allRecipes={allRecipes.length}
                paginado={paginado}
                currentPage={currentPage}
                className={style.Paginado}
            />
           
        </div>    
            <div className={style.Cards}>
                {
                    currentRecipes.length ?
                    currentRecipes && currentRecipes.map(e => {
                        console.log(currentRecipes)
                        
                        return( 
                            <Link to={"/home/" + e.id} className={style.Link}>
                                <Card title={e.title} image={e.image ? e.image : <img src="https://i.pinimg.com/564x/3b/33/b9/3b33b9158df26f355424259cc38b5c14.jpg"></img>} diets={e.diets} ></Card>
                            </Link>
                            
                        )
                        
                    }) :
                    <div>Cargando...</div>
                } 
            </div>
            
        
        </div>
    )
}